import React, { Component } from 'react';
import axios from 'axios';
import { Redirect } from 'react-router-dom';

import './NewPost.css';

class NewPost extends Component {
    state = {
        
        title: '',
        content: '',
        type: '',
        answer:'',
        
        
        submitted: false
    }

    componentDidMount () {
        // If unauth => this.props.history.replace('/posts');
        console.log( this.props );
    }

    postDataHandler = () => {
        const data = {
            title: this.state.title,
            body: this.state.content,
            type: this.state.type,
            answer: this.state.answer,
            
            
        };
        axios.post('/posts.json', data )
            .then( response => {
                // console.log( response );
                //const key = Object.keys(response.data);
                // console.log('KEY' + key);
                this.props.history.replace('/posts/');
                this.setState( { submitted: true } );
            } );
    }

    render () {
        let redirect = null;
        if (this.state.submitted) {
            redirect = <Redirect to="/posts/" />;
        }
        return (
            <div className="NewPost">
                {redirect}
                <h1>Ask A Question!</h1>
                <label>Title</label>
                <input type="text" value={this.state.title} onChange={( event ) => this.setState( { title: event.target.value } )} />
                <label>Content</label>
                <textarea rows="4" value={this.state.content} onChange={( event ) => this.setState( { content: event.target.value } )} />
                <label>Type</label>
                <select value={this.state.type} onChange={( event ) => this.setState( { type: event.target.value } )}>
                    {/* <option value="Max">Max</option>
                    <option value="Manu">Manu</option> */}
                    <option value="401k">401k</option>
                    <option value="RothIRA">Roth IRA</option>
                    <option value="Individual-investment">Individual Investment Account</option>
                    <option value="Crypto">Crypto</option>
                    <option value="Savings">Savings</option>
                    <option value="Credit">Credit</option>
                </select>
                <button onClick={this.postDataHandler}>Add Post</button>
            </div>
        );
    }
}

export default NewPost;